import React from 'react';
import { Reminder } from '../interfaces';
import util from '../utils/util';
import { EDIT_FORM_URL } from '../utils/constants';
import { Route } from 'react-router-dom';
import { History } from 'history';

interface Props {
	reminder: Reminder;
	isSeen: boolean;
	babyId: number;
};

export class ReminderCol extends React.Component<Props, {}> {
	onReminderClick = (history: History) => {
		const params = {
			reminderId: this.props.reminder.id,
			babyId: this.props.babyId
		};
	
		const paramsStr = util.paramsObjToUrl(params);
	

		const editFormFullUrl = `${EDIT_FORM_URL}?${paramsStr}`;
		history.push(editFormFullUrl);
	}

	render() {
		const {reminder, isSeen} = this.props;
		const isReminder = !!reminder;

		return (
			isReminder ? 
			<Route path="" render={({history}) => (
				<td onClick={() => this.onReminderClick(history)} className={`reminder_td ${isSeen ? 'seen' : ''}`} data-id={reminder.id}>
					{reminder.name}
				</td>
			)} /> :
			<td onClick={() => alert('אין כאן תזכורת הניתנת לעריכה :)')}>
				-
			</td>
		);
	}
}